import React from "react";

import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import { useNavigate } from "react-router-dom";


function Signin() {
  let emailRef = React.useRef('');
  let passwordRef = React.useRef('');
  let navigate = useNavigate();
  // let [result, setResult] = React.useState({});
  let [status, setStatus] = React.useState("");

  const handleLoginSubmit = (evt) => {
    evt.preventDefault();
    setStatus("Sending data...");
    const loginObject = {
      email: emailRef.current.value,
      password: passwordRef.current.value,
    };
    try {
      import("../../model/db").then(async (db) => {
        const res = await db.readById(loginObject.email, "users");
        if (res.email === loginObject.email & res.password === loginObject.password) {
          window.sessionStorage.setItem("user", res.email);
          navigate("/recruiter/users");
        }else{
          setStatus("email or password invalid");
        }
      });
    } catch (error) {
      console.warn(error);
    }

  }
  return (
    <Container className="w-50">
      <Form onSubmit={(ev) => handleLoginSubmit(ev)}>
        <Form.Group className="mb-3" controlId="email">
          <Form.Label>Email address</Form.Label>
          <Form.Control type="email" placeholder="Enter email" ref={emailRef} />
          <Form.Text className="text-muted">
            We'll never share your email with anyone else.
          </Form.Text>
        </Form.Group>

        <Form.Group className="mb-3" controlId="password">
          <Form.Label>Password</Form.Label>
          <Form.Control type="password" placeholder="Password" ref={passwordRef} />
        </Form.Group>
        <Form.Group className="mb-3" controlId="checkbox">
          <Form.Check type="checkbox" label="Check me out" />
        </Form.Group>
        <div className="text-center">
          <span className="text-success d-block">{status}</span>
          <Button variant="success" type="submit" className="btn-sm">
            Log in
          </Button>
        </div>

      </Form>
    </Container>
  );
}

export default Signin;